import React from 'react'

/*
const defaultOptions = {
    duration: 3000,                  // default
    width: "15em",                   // default
    my: "center bottom",             // default
    at: "center bottom",             // default
    of: window,                      // default
    offset: "0 0",                   // default
    collision: "fit fit",            // default
    onClose: null,                   // default
    autoClose: true,                 // default
    animationTimingFunction: "ease", // default
    animationDuration: 1000,         // default
    closeOnBrowserNavigation: true,  // default
    type: 'success' //one of Success/Error/Warning/Info
}
*/
export default class Toast extends React.Component {
  static defaultProps = {
    message: '',
    duration: 3000,                  // default
    width: "15em",                   // default
    my: "center bottom",             // default
    at: "center bottom",             // default
    of: window,                      // default
    offset: "0 0",                   // default
    collision: "fit fit",            // default
    onClose: null,                   // default
    autoClose: true,                 // default
    animationTimingFunction: "ease", // default
    animationDuration: 1000,         // default
    closeOnBrowserNavigation: true,  // default
    type: 'success' //one of Success/Error/Warning/Info
  }

  constructor(props) {
    super(props);
    this.state = {}
    this.container = React.createRef()
  }

  getIconByType (type) {
    if (type) {
      return <span className={"sapUiIcon " + type}></span>
    }
  }

  componentDidMount() {
    this.container.current.style.opacity = 1
    this.startTimer()
  }

  componentWillUnmount() {
    this.stopTimer()
  }

  onClose() {
    this.stopTimer()
    this.props.willUnmount()
  }

  startTimer() {
		if (this.props.duration > 0) {
			this.timeout = setTimeout(() => {
				this.onClose()
			}, this.props.duration)
		} else {
      this.onClose()
    }
  }

  stopTimer() {
    clearTimeout(this.timeout)
  }

  render () {
    let {
      message,
      // duration,
      // width,
      // my,
      // at,
      // of,
      // offset,
      // collision,
      // onClose,
      // autoClose,
      // animationTimingFunction,
      // animationDuration,
      // closeOnBrowserNavigation,
      type
    } = this.props
    return (
      <div ref={this.container} className="sap-eureka-ui-framework-toast-instance" style={{opacity: 0}}>
        {
          this.getIconByType(type)
        }
        {message}
      </div>
    )
  }
}