import utils from "./utils"
/**
 * 
 * We Support all kinds of registration. For example,
 * it can be an Object, like following, then you can use convenience method renderFragment
Eureka.FragmentManager.registerFragment(process.env.REACT_APP_NAME + 'ReactFragmentSample1', {
  render (container, props, history) {
    let dom = typeof container === 'string' ? document.getElementById(container) : container
    ReactDOM.render(<ReactFragmentSample1 history={history} {...(props || {})} />, dom)
  },
  unmount (container) {
    let dom = typeof container === 'string' ? document.getElementById(container) : container
    ReactDOM.unmountComponentAtNode(dom)
  }
})
 * or you can just regist a component itself
 * like following
 Eureka.FragmentManager.registerFragment(process.env.REACT_APP_NAME + 'Profile', ReactFragmentSample1)
 */
const uniqueDict = utils.generateUniqueDict()
const Suffix = '_Fragment'
const register = (name, configuration) => {
  const fragment = uniqueDict.getItem(name + Suffix);
  fragment.configuration = configuration;
}
const getFragment = name => {
  const fragment = uniqueDict.getItem(name + Suffix);
  return fragment && fragment.configuration
}
const renderFragment =  (name, ...rest) => {
  const func =  uniqueDict.findFuncInConfiguration(name + Suffix, 'render')
  if (func) {
    func.apply(null, rest)
  }
}
const unmountFragment =  (name, ...rest) => {
  const func = uniqueDict.findFuncInConfiguration(name + Suffix, 'unmount')
  if (func) {
    func.apply(null, rest)
  }
}
const getFragmentsWithTag = (tag, config) => {
  let { components } = config;
  let tagFragments = [];
  components.forEach(component => {
    if (
      component &&
      component.config &&
      Array.isArray(component.config.fragments) &&
      component.config.fragments.length > 0
    ) {
      component.config.fragments.forEach(fragment => {
        if (
          Array.isArray(fragment.section) &&
          fragment.section.length > 0 &&
          fragment.section.indexOf(tag) >= 0
        ) {
          let register = getFragment(component.config.app + fragment.name);
          tagFragments.push(register);
        }
      });
    }
  });
  return tagFragments;
};
export default {
  getFragmentsWithTag,
  register,
  registerFragment: register,
  get: getFragment,
  getFragment,
  render: renderFragment,
  renderFragment: renderFragment,
  unmount: unmountFragment,
  unmountFragment,
}