import React from 'react';
import AppManager from '@/eureka/app-manager'
import DefaultLayout from './DefaultLayout';

class MicroFrontend extends React.Component {
  componentDidMount() {
    const { name, host, document } = this.props;
    const scriptId = `micro-frontend-script-${name}`;

    if (document.getElementById(scriptId)) {
      this.renderMicroFrontend();
      return;
    }

    fetch(`${host}/asset-manifest.json`)
      .then(res => res.json())
      .then(manifest => {
        const script = document.createElement('script');
        script.id = scriptId;
        script.crossOrigin = '';
        script.src = `${host}${manifest.files['main.js']}`;
        script.onload = this.renderMicroFrontend;
        document.head.appendChild(script);
      });
  }

  componentWillUnmount() {
    const { name} = this.props;
    AppManager.unmountApp(`${name.toLowerCase()}`, `${name}-container`)
    // window[`unmount${name}`](`${name}-container`);
    // window.eureka[`unmount${name}`](`${name}-container`);
  }

  renderMicroFrontend = () => {
    const { name, history } = this.props;
    AppManager.renderApp(`${name.toLowerCase()}`, `${name}-container`, history)
    // window[`render${name}`](`${name}-container`, history);
    // window.eureka[`render${name}`](`${name}-container`, history);
  };

  render() {
    // console.log(`${this.props.name}-container`);
    const container = <div id={`${this.props.name}-container`} />;
    return <DefaultLayout children={container} history={this.props.history} />;
  }
}

MicroFrontend.defaultProps = {
  document,
  window,
};

export default MicroFrontend;
