

import React from 'react';
import ReactDOM from 'react-dom';
import Toast from './Toast'
const defaultOptions = {
    duration: 3000,                  // default
    width: "15em",                   // default
    my: "center bottom",             // default
    at: "center bottom",             // default
    of: window,                      // default
    offset: "0 0",                   // default
    collision: "fit fit",            // default
    onClose: null,                   // default
    autoClose: true,                 // default
    animationTimingFunction: "ease", // default
    animationDuration: 1000,         // default
    closeOnBrowserNavigation: true,  // default
    type: 'success' //one of Success/Error/Warning/Info
}

const queue = []

const toastLayerId = 'sap_eureka_ui_framework_toast_layer'
const totastLayerClass = 'sap-eureka-ui-framework-toast-layer'
const totastLayerIndex = 100
let toastLayer = null

let inAnimation = false

function tryShowToast () {
  if (!inAnimation && queue.length > 0) {
    lazyInitLayer()
    let options = queue.shift()
    const component = React.createElement(Toast, Object.assign(options, {
      willUnmount: () => {
        inAnimation = false
        ReactDOM.unmountComponentAtNode(toastLayer)
        tryShowToast()
      }
    }))
    ReactDOM.render(component, toastLayer)
    inAnimation = true
  }
}

function lazyInitLayer () {
  if (!toastLayer) {
    toastLayer = document.createElement('div')
    toastLayer.className = totastLayerClass
    toastLayer.id = toastLayerId
    toastLayer.style.zIndex = totastLayerIndex
    document.body.appendChild(toastLayer)
  }
}

export default {
  show (message, options) {
    let optionConfig = Object.assign({ message }, defaultOptions, options)
    queue.push(optionConfig)
    tryShowToast()
  },
  success (message, options) {
    this.show(message, Object.assign({}, { type: 'success'}, options))
  },
  error (message, options) {
    this.show(message, Object.assign({}, { type: 'error'}, options))
  },
  warn (message, options) {
    this.show(message, Object.assign({}, { type: 'warn'}, options))
  },
  info (message, options) {
    this.show(message, Object.assign({}, { type: 'info'}, options))
  }
}