import utils from "./utils"
const uniqueDict = utils.generateUniqueDict();
const registerApp = (appName, configuration) => {
  const app = uniqueDict.getItem(appName)
  app.configuration = configuration
}
const renderApp = (appName, ...rest) => {
  const func =  uniqueDict.findFuncInConfiguration(appName, 'render')
  if (func) {
    func.apply(null, rest)
  }
}
const unmountApp = (appName, ...rest) => {
  const func = uniqueDict.findFuncInConfiguration(appName, 'unmount')
  if (func) {
    func.apply(null, rest)
  }
}
const getComponent = (appName) => {
  const app = uniqueDict.getItem(appName)
  return app && app.configuration && app.component
}
export default {
  get: getComponent,
  getComponent,
  register: registerApp,
  registerApp,
  render: renderApp,
  renderApp,
  unmount: unmountApp,
  unmountApp,
}