import i18n from 'i18next';
import Backend from 'i18next-xhr-backend';
import LanguageDetector from 'i18next-browser-languagedetector';
import * as reactI18next from 'react-i18next';

// https://react.i18next.com/latest/using-with-hooks
// https://react.i18next.com/latest/withtranslation-hoc
// https://github.com/i18next/react-i18next/blob/master/example/react/src/i18n.js

// to remove default translation namespace
// i18n.setDefaultNamespace('shell');
// i18n.init({
//   lng: 'en',
//   defaultNS: 'shell',
//   preload: ['en'],
//   load: 'languageOnly',
//   // fallbackLng: 'en',
//   fallbackLng: false,
// });

const initI18n = (config, options) => {
  console.log(process.env.NODE_ENV);
  i18n
    // load translation using xhr -> see /public/locales
    // learn more: https://github.com/i18next/i18next-xhr-backend
    .use(Backend)
    // detect user language
    // learn more: https://github.com/i18next/i18next-browser-languageDetector
    .use(LanguageDetector)
    // pass the i18n instance to react-i18next.
    .use(reactI18next.initReactI18next)
    // init i18next
    // for all options read: https://www.i18next.com/overview/configuration-options
    .init(
      Object.assign(
        {
          lng: 'en',
          defaultNS: 'translation',
          preload: ['en'],
          load: 'languageOnly',
          // fallbackLng: 'en',
          fallbackLng: false,
          debug: process.env.NODE_ENV !== 'production', // true
          lowerCaseLng: true,
          react: {
            useSuspense: false,
          },
          interpolation: {
            escapeValue: false, // not needed for react as it escapes by default
          },
          detection: {
            order: ['path', 'localStorage'],
          },
          backend: {
            loadPath: (lng, ns) => {
              console.log(lng[0], ns[0]);

              // for prod
              // {
              //   shell:
              //     'https://cdn.eurekacloud.io/static/eureka-shell-ui/e00cc343611a1eb35437f67b095859334d481ce0/',
              //   glory:
              //     'https://cdn.eurekacloud.io/static/glory-ui/e00cc343611a1eb35437f67b095859334d481ce0/',
              // }

              const mockConfig = {
                translation: 'http://localhost:2020',
                glory: 'http://localhost:2021',
              };

              if (!config) {
                config = mockConfig;
              }

              console.log(config, options);

              if (ns[0] !== '') {
                return process.env.NODE_ENV !== 'production'
                  ? `${config[ns[0]]}/locales/{{lng}}/{{ns}}.json`
                  : `${config[ns[0]]}/locales/{{lng}}/{{ns}}.json`;
              }

              return process.env.NODE_ENV !== 'production'
                ? '/locales/{{lng}}/{{ns}}.json'
                : '/locales/{{lng}}/{{ns}}.json';
            },
            // allow cross domain requests
            crossDomain: true,

            // allow credentials on cross domain requests
            withCredentials: false,

            // overrideMimeType sets request.overrideMimeType("application/json")
            overrideMimeType: false,

            // custom request headers sets request.setRequestHeader(key, value)
            // customHeaders: {
            // authorization: 'foo',
            // },
          },
        },
        options,
      ),
    );
};

export default {
  i18n,
  initI18n,
  ...reactI18next,
};
